				let content = '扫码关注-天天省钱拿返利\n早餐便宜5块！\n午餐便宜4块！\n下午茶便宜5块！\n夜宵便宜10块！\n别犹豫，别控制！\n喜欢订餐—越吃越省\n喜欢买货—越买越省' // 复制内容，必须字符串，数字需要转换为字符串
				const result = h5Copy(content)
				if (result === false) {
